.reset-container {
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  justify-content: center;
  margin: 46px 0;
}

.reset-wrap {
  padding: 32px 50px;
  /* max-width: 650px; */
  border-radius: 60px;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.1);
}

.reset-header {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: #074979;
}

.reset-des {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 16px;
}

.reset-resend-code {
  text-align: right;
  color: rgba(0, 0, 0, 0.8);
  margin: 0px 0;
}

.reset-resend-code:hover {
  color: #074979;
  cursor: pointer;
}
.reset-form-button {
  min-width: 300px;
  border-radius: 35px;
  background-color: #074979;
  color: white;
}
.reset-form-button:hover {
  background-color: #8fc2e6 !important;
  color: white;
}
