.forgot-container {
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  justify-content: center;
  margin: 30px 0;
  height: 100%;
}

.forgot-wrap {
  padding: 32px 60px;
  /* max-width: 650px; */
  border-radius: 60px;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.1);
}

.forgot-header {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: #074979;
}

.forgot-des {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 16px;
}

.forgot-resend-code {
  text-align: right;
  color: rgba(0, 0, 0, 0.8);
  margin: 0px 0;
}

.forgot-resend-code:hover {
  color: #074979;
  cursor: pointer;
}
.forgot-form-button {
  min-width: 300px;
  border-radius: 35px;
  background-color: #074979;
  color: white;
}
.forgot-form-button:hover {
  background-color: #8fc2e6 !important;
  color: white;
}
