.register-container {
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  justify-content: center;
  margin: 30px 0;
}
.register-wrap {
  padding: 20px;
  /* max-width: 650px; */
  border-radius: 60px;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.1);
}

.register-header {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: #074979;
}

.register-icon {
  color: #074979;
}

.register-btn {
  min-width: 300px;
  background-color: white;
  color: black;
  margin-left: 100px;
  border: 1px solid #000;
  border-radius: 5px;
}
.register-btn:hover {
  background-color: #8fc2e6 !important;
  color: white;
}

.register-check {
  margin-left: 100px;
}
.register-btn-regis {
  margin-left: 67px;
  min-width: 375px;
  background-color: #074979;
  color: white;
  border-radius: 35px;
}
.register-btn-regis:hover {
  background-color: #8fc2e6 !important;
  color: white;
}

.register-to-login {
  text-align: center;
  font-size: 14px;
}

.register-link {
  color: #074979;
  /* text-decoration: none; */
  font-weight: 600;
  margin-left: 20px;
}
